import { bindable, containerless } from 'aurelia-framework';

@containerless
export class EmChatSidebarRight {

    @bindable loginUser;
    @bindable isAt;
    @bindable channel;

    actionMapping = {
        [nsCons.ACTION_TYPE_DIR]: { handler: this.dirHandler, nodata: '', show: 'dir', icon: 'unordered list', title: '' },
        [nsCons.ACTION_TYPE_AT]: { nodata: '@', show: 'msg', icon: 'at', title: '' },
        [nsCons.ACTION_TYPE_STOW]: { nodata: '', show: 'msg', icon: 'empty star', title: '' },
        [nsCons.ACTION_TYPE_ATTACH]: { handler: this.attachHandler, nodata: '', show: 'attach', icon: 'attach', title: '' },
        [nsCons.ACTION_TYPE_SCHEDULE]: { handler: this.scheduleHandler, nodata: '', show: 'schedule', icon: 'calendar outline', title: '' },
        [nsCons.ACTION_TYPE_SEARCH]: { nodata: '', show: 'msg', icon: 'search', title: '' },
        [nsCons.ACTION_TYPE_PIN]: { nodata: '', show: 'msg', icon: 'pin', title: '' },
        [nsCons.ACTION_TYPE_TOPIC]: { nodata: '', show: 'topic', icon: 'talk outline', title: '' },
        [nsCons.ACTION_TYPE_TODO]: { handler: this.todoHandler, nodata: '', show: 'todo', icon: 'list', title: '' },
    }

    /**
     * 
     */
    constructor() {

        this.subscribe = ea.subscribe(nsCons.EVENT_CHAT_RIGHT_SIDEBAR_TOGGLE, (payload) => {
            this.actived = _.clone(this.actionMapping[payload.action]);
            this.actived.payload = payload;
            if (this.actived.handler) {
                _.bind(this.actived.handler, this, payload)();
            }
        });

        this.subscribe1 = ea.subscribe(nsCons.EVENT_CHAT_RIGHT_SIDEBAR_SCROLL_TO, (payload) => {
            $('.em-chat-sidebar-right div[ref="scrollbarRef"]').scrollTo(payload, 120);
        });

    }

    /**
     * 
     */
    unbind() {
        this.subscribe.dispose();
        this.subscribe1.dispose();
    }

    attachHandler(payload) {
        this.chatAttachVm.fetch();
    }

    dirHandler(payload) {
        $(this.dirRef).empty().append(payload.result);
    }

    scheduleHandler(payload) {
        this.chatScheduleVm.show();
    }

    todoHandler(payload) {
        // this.chatScheduleVm.show();
    }

    closeHandler() {
        ea.publish(nsCons.EVENT_CHAT_TOGGLE_RIGHT_SIDEBAR, {});
    }

}
